    </div>
  </div>
</template>
<script>
let delay = {
  interval: 0.05,
  start: 0
};
let reverseDelay = {
  interval: 0.0075,
  start: 28
};
export default {
  data() {
    return {
      childCount: 0,
      delay: delay,
      invertColors: true
    };
  },
  props: ["pageOpen", "isApple"],
  mounted() {
    Object.entries(this.$refs).forEach(([key, value]) => {
      for (let path of value.children) {
        this.childCount++;
      }
    });
    if (this.pageOpen) {
      this.delay = reverseDelay;
      this.delay.start = this.childCount;
    } else {
      this.delay = delay;
    }
    let currentDelay = this.delay.interval * this.delay.start;
    Object.entries(this.$refs).forEach(([key, value]) => {
      for (let path of value.children) {
        path.style.animationDelay = `${currentDelay}s`;
        if (!this.pageOpen) {
          currentDelay += this.delay.interval;
        } else {
          currentDelay -= this.delay.interval;
        }
      }
    });
  }
};
</script>



